import {Cell} from '../../model/cell';
import {create2darray} from '../../framework/framework';

export default class NonogramData {
  static createNewNonogram(numberRows, numberColumns, numberColors) {
    let nonogram = new NonogramData();
    nonogram.name = '';
    nonogram.nameEnglish = '';
    nonogram.numberRows = numberRows;
    nonogram.numberColumns = numberColumns;
    nonogram.numberColors = numberColors;
    nonogram.colors = ['black', '#f44336', '#b71c1c', '#ffc107', '#303f9f', '#03a9f4', '#7b1fa2', '#c2185b', '#388e3c', '#00e676'].map(color => ({hex: color}));
    // номера цвета или -1
    nonogram.field = [[]];
    nonogram.createTimestamp = Date.now();
    nonogram.extendFieldIfRequired();
    return nonogram;
  }

  extendFieldIfRequired() {
    let numberRowsOld = this.field.length;
    let numberColumnsOld = this.field[0].length;
    if (numberRowsOld >= this.numberRows && numberColumnsOld >= this.numberColumns) {
      return;
    }

    let numberRows = Math.max(this.numberRows, numberRowsOld);
    let numberColumns = Math.max(this.numberColumns, numberColumnsOld);
    let field = create2darray(numberRows, numberColumns);
    for (let i = 0; i < numberRows; ++i) {
      for (let j = 0; j < numberColumns; ++j) {
        let existsOldCell = i < numberRowsOld && j < numberColumnsOld;
        field[i][j] = existsOldCell ? this.field[i][j] : -1;
      }
    }
    this.field = field;
  }
}



