<template>
  <div>
    <div v-for="i of _.range(a.length)">{{a[i].v}}</div>
    <div v-for="ai of a">{{ai.v}}</div>
    <v-btn @click="a[0].v += 1">clickA</v-btn>
    <v-btn @click="a.push({v: 0})">pushA</v-btn>
    <div v-for="i of _.range(b.length)">{{b[i]}}</div>
    <div v-for="bi of b">{{bi}}</div>
    <v-btn @click="b[0] += 1">clickB</v-btn>
    <v-btn @click="b.push(0)">pushB</v-btn>
  </div>
</template>

<style>

</style>

<script>
  export default {
    data() {
      return {
        a: [
          {v: 1},
          {v: 2},
          {v: 3}
        ],
        b: [1, 2, 3]
      };
    }
  };
</script>



