import 'mousetrap';
import {create2darray} from '../framework/framework';

const State = {
  UNKNOWN: {id: 0, cssClass: 'unknown'},
  FILLED: {id: 1, cssClass: 'filled'},
  EMPTY: {id: 2, cssClass: 'empty'}
};

class Cell {
  constructor(i, j) {
    this.i = i;
    this.j = j;
    this.state = State.UNKNOWN;
  }

  setUnknown() {
    this.state = State.UNKNOWN;
  }

  setFilled() {
    this.state = State.FILLED;
  }

  setEmpty() {
    this.state = State.EMPTY;
  }
}

export class NonogramInput {
  constructor(nonogramData) {
    this.numberRows = nonogramData.numberRows;
    this.numberColumns = nonogramData.numberColumns;
    this.createField();
    this.selectedCell = this.field[0][0];
    this.showSelectedCell = false;
    this.bindKeyboardActions();
  }

  createField() {
    this.field = create2darray(this.numberRows, this.numberColumns);
    for (let i = 0; i < this.numberRows; ++i) {
      for (let j = 0; j < this.numberColumns; ++j) {
        this.field[i][j] = new Cell(i, j);
      }
    }
  }

  bindKeyboardActions() {
    Mousetrap.bind('up', () => this.moveSelectedCell(-1, 0));
    Mousetrap.bind('down', () => this.moveSelectedCell(+1, 0));
    Mousetrap.bind('left', () => this.moveSelectedCell(0, -1));
    Mousetrap.bind('right', () => this.moveSelectedCell(0, +1));
    // Mousetrap.bind('enter', () => this.selectedCell.toggleUnknownEmpty());
    Mousetrap.bind('e', () => this.selectedCell.setUnknown(), 'keydown');
    Mousetrap.bind('f', () => this.selectedCell.setFilled(), 'keydown');
    Mousetrap.bind('a', () => this.selectedCell.setEmpty(), 'keydown');
  }

  moveSelectedCell(deltaI, deltaJ) {
    let newI = Math.max(0, Math.min(this.numberRows - 1, this.selectedCell.i + deltaI));
    let newJ = Math.max(0, Math.min(this.numberColumns - 1, this.selectedCell.j + deltaJ));
    this.selectedCell = this.field[newI][newJ];
    this.showSelectedCell = true;
  }

  click(cell) {
    cell.setFilled();
    this.selectedCell = cell;
    this.showSelectedCell = false;
  }

  rightClick(cell) {
    cell.setEmpty();
    this.selectedCell = cell;
    this.showSelectedCell = false;
  }

  isSelected(cell) {
    return this.showSelectedCell && cell === this.selectedCell;
  }

  isCellEmpty(cell) {
    return cell.state === State.EMPTY;
  }
}



