<template>
  <div class="fullscreen">
    <nonogram-table v-if="nonogramData" :nonogramData="nonogramData"></nonogram-table>
  </div>
</template>

<style scoped>
  .fullscreen {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>

<script>
  import axios from 'axios';
  import {NonogramData} from '../model/NonogramData';
  import NonogramTable from './NonogramTable';

  export default {
    components: {NonogramTable},
    data() {
      return {
        nonogramData: null
      };
    },
    created() {
      this.fetchData();
    },
    methods: {
      async fetchData() {
        let field = (await axios.get(`http://localhost/nonograms/black-white/${this.$route.params.id}`)).data;
        this.nonogramData = new NonogramData(field);
      }
    }
  };
</script>



