import {transposeMatrix} from '../framework/framework';

export class NonogramData {
  constructor(field) {
    this.field = field;
    this.headerLeft = this.createHeaderLeft(field);
    this.headerTop = transposeMatrix(this.createHeaderLeft(transposeMatrix(field)));
  }

  get numberRows() {
    return this.field.length;
  }

  get numberColumns() {
    return this.field[0].length;
  }

  createHeaderLeft(field) {
    let numberRows = field.length;
    let numberColumns = field[0].length;

    let header = new Array(numberRows);
    for (let i = 0; i < numberRows; ++i) {
      let currentRow = header[i] = [];
      for (let j = 0; j < numberColumns; ++j) {
        if (field[i][j]) {
          if (j === 0 || !field[i][j - 1]) {
            currentRow.push(1);
          } else {
            currentRow.push(currentRow.pop() + 1);
          }
        }
      }
    }

    let headerRows = Math.max(...header.map(row => row.length));
    header = header.map(row => new Array(headerRows - row.length).concat(row));
    return header;
  }
}



