import NonogramData from './NonogramData';

class NonogramCreatorStorage {
  constructor() {
    // localStorage.clear();
  }

  get numberNonograms() {
    let numberNonograms = localStorage.getItem('number_nonograms');
    return numberNonograms === null ? 0 : +numberNonograms;
  }

  set numberNonograms(value) {
    localStorage.setItem('number_nonograms', value);
  }

  get lastNonogramId() {
    return localStorage.getItem('last_nonogram_id');
  }

  set lastNonogramId(value) {
    if (value === null) {
      localStorage.removeItem('last_nonogram_id');
    } else {
      localStorage.setItem('last_nonogram_id', value);
    }
  }

  getNonogramKey(nonogramIndex) {
    return `nonogram_${nonogramIndex}`;
  }

  getLastNonogram() {
    let lastNonogramId = this.lastNonogramId;
    if (lastNonogramId === null) {
      return null;
    }
    return this.getNonogramById(lastNonogramId);
  }

  getNonogramById(nonogramId) {
    let nonogramKey = this.getNonogramKey(nonogramId);
    let nonogramDTO = JSON.parse(localStorage.getItem(nonogramKey));
    let nonogram = Object.assign(new NonogramData(), nonogramDTO);
    // console.log('getNonogramById', nonogramId, nonogram);
    return nonogram;
  }

  saveNonogram(nonogram) {
    if (nonogram.id === undefined) {
      let numberNonograms = this.numberNonograms;
      nonogram.id = this.numberNonograms;
      this.numberNonograms = numberNonograms + 1;
    }
    localStorage.setItem(this.getNonogramKey(nonogram.id), JSON.stringify(nonogram));
    this.lastNonogramId = nonogram.id;
  }

  getAllNonograms() {
    return _.range(0, this.numberNonograms).map(nonogramId => this.getNonogramById(nonogramId));
  }
}

export let nonogramCreatorStorage = new NonogramCreatorStorage();



