<template>
  <div class="wrapper">
    <div class="nonogram" ref="nonogram">
      <table class="main">
        <tr>
          <td class="corner">
            <div></div>
          </td>
          <td>
            <table class="field header horizontal-blocks-border">
              <tr v-for="row of nonogramData.headerTop">
                <td v-for="number of row">{{number}}</td>
              </tr>
            </table>
          </td>
        </tr>
        <tr>
          <td>
            <table class="field header vertical-blocks-border">
              <tr v-for="row of nonogramData.headerLeft">
                <td v-for="number of row">{{number}}</td>
              </tr>
            </table>
          </td>
          <td>
            <table class="field horizontal-blocks-border vertical-blocks-border">
              <tr v-for="(row, i) of nonogramInput.field">
                <td
                  v-for="(cell, j) of row"
                  :class="[{selected: nonogramInput.isSelected(cell)}, cell.state.cssClass]"
                  @contextmenu.prevent="nonogramInput.rightClick(cell)"
                  @click="nonogramInput.click(cell)"
                >
                  <!--<svg v-show="nonogramInput.isCellEmpty(cell)" fill="#009688" height="100" width="100" viewBox="0 0 100 100">-->
                  <svg v-show="nonogramInput.isCellEmpty(cell)" fill="black" height="100" width="100" viewBox="0 0 100 100">
                    <path d="M0,0 L8,0 50,42 92,0 100,0 100,8 58,50 100,92 100,100 92,100 50,58 8,100, 0,100 0,92 42,50 0,8 0,0 0,0"></path>
                  </svg>
                </td>
              </tr>
            </table>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<style lang="scss" scoped>
  $teal5: #e0f2f1;
  $teal4: #b2dfdb;
  $teal3: #80cbc4;
  $teal2: #4db6ac;
  $teal1: #26a69a;
  $teal0: #009688;

  svg {
    height: 100%;
    width: 100%;
  }

  td {
    user-select: none;
  }

  table.main {
    border: 3px solid $teal0;
    border-collapse: collapse;
  }

  table.main td {
    border: 3px solid $teal0;
    padding: 0;
  }

  .corner {
    background-color: $teal5;
  }

  table.main, table.main tr:first-child, .corner, .corner div {
    height: 100%;
  }

  .corner div {
    border: 1px solid $teal2;
  }

  table.header td {
    background-color: $teal4;
    font-size: 32px;
    font-weight: 300;
    line-height: 1;
  }

  table.field {
    table-layout: fixed;
    border-collapse: collapse;
    border: none;
  }

  table.field td {
    height: 40px;
    width: 40px;
    border-collapse: collapse;
    border: 1px solid $teal2;
    text-align: center;
  }

  table.horizontal-blocks-border td:nth-of-type(5n + 5):not(:last-child) {
    border-right: 3px solid $teal2;
  }

  table.vertical-blocks-border tr:nth-of-type(5n + 5):not(:last-child) td {
    border-bottom: 3px solid $teal2;
  }

  td.filled {
    //background-color: $teal0;
    background-color: black;
  }

  td.selected {
    background-color: #1de9b6;
  }

  td.filled.selected {
    background-color: #006064;
    /*background-color: #1de9b6;*/
  }

  table, tr, td {
    box-sizing: content-box;
  }

  svg {
    line-height: 1;
    vertical-align: middle;
  }
</style>

<script>
  import {NonogramInput} from '../model/NonogramInput';

  export default {
    name: 'nonogram-table',
    props: ['nonogramData'],
    data() {
      return {
        nonogramInput: new NonogramInput(this.nonogramData)
      };
    },
    mounted: function () {
      window.addEventListener('resize', this.onResize);
      this.onResize();
    },
    beforeDestroy: function () {
      window.removeEventListener('resize', this.onResize);
    },
    methods: {
      onResize() {
//        TODO
//        let nonogram = this.$refs.nonogram;
//        let size = Math.min(nonogram.parentElement.clientHeight, nonogram.parentElement.clientWidth);
//        nonogram.style.height = size + 'px';
//        nonogram.style.width = size + 'px';
      }
    }
  };
</script>



