<template>
  <v-tabs centered class="mt-3" ref="vTabs" v-model="activeTab">
    <v-tabs-bar class="white">
      <v-tabs-item href="#tabEditor" ripple>Редактор японских кроссвордов</v-tabs-item>
      <v-tabs-item href="#tabList" ripple>Недавно созданные кроссворды</v-tabs-item>
      <v-tabs-slider color="teal"></v-tabs-slider>
    </v-tabs-bar>
    <v-tabs-items>
      <v-tabs-content id="tabEditor">
        <v-card flat>
          <v-card-text>
            <nonogram-creator ref="nonogramCreator"></nonogram-creator>
          </v-card-text>
        </v-card>
      </v-tabs-content>
      <v-tabs-content id="tabList" lazy>
        <v-card flat>
          <v-card-text>
            <nonogram-list v-bind="{startEditNonogram}"></nonogram-list>
          </v-card-text>
        </v-card>
      </v-tabs-content>
    </v-tabs-items>
  </v-tabs>
</template>

<style>

</style>

<script>
  import NonogramCreator from './NonogramCreator.vue';
  import NonogramList from './NonogramList.vue';
  import {nonogramCreatorStorage} from './NonogramCreatorStorage';

  export default {
    components: {NonogramCreator, NonogramList},
    data() {
      return {
        activeTab: 'tabEditor'
//        activeTab: 'tabList'
      };
    },
    methods: {
      startEditNonogram(nonogramId) {
        this.$refs.nonogramCreator.startEditAnotherNonogram(nonogramId);
        nonogramCreatorStorage.lastNonogramId = nonogramId;
        this.activeTab = 'tabEditor';
      }
    }
  };
</script>



