<template>
  <div class="hello">
    <div class="canvasWraper" ref="canvasWrapper">
      <canvas ref="canvas"></canvas>
    </div>
  </div>
</template>

<style scoped>
  .hello {
    height: 100%;
    /*background: aquamarine;*/
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .canvasWraper {
    height: 77%;
    width: 77%;
  }

  canvas {
    height: 100%;
    width: 100%;
  }
</style>

<script>
  let rows = [[7], [1, 1], [1, 1], [1, 1], [7], [6, 6], [15], [2, 2], [1, 1, 1, 1], [1, 1], [1, 5, 1], [3, 1, 3], [3], [3], [1]];
  let columns = [[1], [2], [2, 1], [3, 1], [10, 1], [1, 3, 2], [1, 3, 1, 1, 2], [1, 1, 1, 5], [1, 3, 1, 1, 2], [1, 3, 2], [10, 1], [3, 1], [2, 1], [2], [1]];
  let nonogramRows = rows.length;
  let nonogramColumns = columns.length;
  let headerRows = 5;
  let headerColumns = 4;

  let outerBorderSize = 2;
  let fieldBorderSize = 1;
  let nonogramBorderSize = fieldBorderSize;
  let headerBorderSize = fieldBorderSize;
  let betweenBorderSize = 1;
  let borderSizes = outerBorderSize + nonogramBorderSize + headerBorderSize + betweenBorderSize;
  let lineSize = 1;
  const getCellsSize = (numberCells, cellSize) => numberCells * (cellSize + lineSize) - lineSize;

  let tealLighten5 = '#e0f2f1';
  let tealLighten4 = '#b2dfdb';
  let tealLighten3 = '#80cbc4';
  let tealLighten2 = '#4db6ac';
  let tealLighten1 = '#26a69a';
  let teal = '#009688';

  let fieldBorderColor = tealLighten2;

  class Draw {
    constructor(canvas) {
      this.canvas = canvas;
      this.context = canvas.getContext('2d');
      this.onResize();
    }

    onResize() {
      this.canvas.height = this.canvas.clientHeight;
      this.canvas.width = this.canvas.clientWidth;
//
//      this.canvas.style.height = this.canvas.height + 'px';
//      this.canvas.style.width = this.canvas.width + 'px';
    }

    get height() {
      return this.canvas.height;
    }

    get width() {
      return this.canvas.width;
    }

    calculateSizes() {
      let availableHeight = this.height - borderSizes * 2 + lineSize * 2;
      let availableWidth = this.width - borderSizes * 2 + lineSize * 2;

      let cellHeight = Math.round(availableHeight / (nonogramRows + headerRows)) - lineSize;
      let cellWidth = Math.round(availableWidth / (nonogramColumns + headerColumns)) - lineSize;
      let cellSize = Math.min(cellHeight, cellWidth);
      return cellSize;
    }

    draw() {
      let cellSize = this.calculateSizes();
      let headerWidth = getCellsSize(headerColumns, cellSize) + headerBorderSize * 2;
      let headerHeight = getCellsSize(headerRows, cellSize) + headerBorderSize * 2;
      let nonogramWidth = getCellsSize(nonogramColumns, cellSize) + nonogramBorderSize * 2;
      let nonogramHeight = getCellsSize(nonogramRows, cellSize) + nonogramBorderSize * 2;

      let filledHeight = headerHeight + nonogramHeight + betweenBorderSize + outerBorderSize * 2;
      let filledWidth = headerWidth + nonogramWidth + betweenBorderSize + outerBorderSize * 2;

      let marginTop = Math.round((this.height - filledHeight) / 2);
      let marginLeft = Math.round((this.width - filledWidth) / 2);
      this.context.translate(marginLeft, marginTop);

//      внешняя рамка
      this.context.fillStyle = teal;
      this.context.fillRect(0, 0, filledWidth, filledHeight);
      this.context.translate(outerBorderSize, outerBorderSize);

//      верхний левый угол
      this.context.fillStyle = fieldBorderColor;
      this.context.fillRect(0, 0, headerWidth, headerHeight);
      this.context.fillStyle = tealLighten5;
      this.context.fillRect(fieldBorderSize, fieldBorderSize, headerWidth - fieldBorderSize * 2, headerHeight - fieldBorderSize * 2);

//      клеточные поля
      let headerCellColor = tealLighten4;
      this.drawField(headerWidth + betweenBorderSize, 0, headerRows, nonogramColumns, headerCellColor, headerBorderSize, cellSize);
      this.drawField(0, headerHeight + betweenBorderSize, nonogramRows, headerColumns, headerCellColor, headerBorderSize, cellSize);
      this.drawField(headerWidth + betweenBorderSize, headerHeight + betweenBorderSize, nonogramRows, nonogramColumns, 'white', headerBorderSize, cellSize);
    }

    drawField(x, y, numberRows, numberColumns, cellColor, borderSize, cellSize) {
      this.context.save();
      this.context.translate(x, y);

//        рамка
      let fieldHeight = getCellsSize(numberRows, cellSize) + borderSize * 2;
      let fieldWidth = getCellsSize(numberColumns, cellSize) + borderSize * 2;
      this.context.fillStyle = fieldBorderColor;
      this.context.fillRect(0, 0, fieldWidth, fieldHeight);

//        поле
      this.context.translate(borderSize, borderSize);
      this.context.fillStyle = cellColor;
      for (let i = 0; i < numberRows; ++i) {
        for (let j = 0; j < numberColumns; ++j) {
          let y = i * (cellSize + lineSize);
          let x = j * (cellSize + lineSize);
          this.context.fillRect(x, y, cellSize, cellSize);
        }
      }
      this.context.restore();
    };
  }

  export default {
    name: 'nanogram',
    data() {
      return {
        height: rows.length,
        width: columns.length
      };
    },
    mounted: function () {
      console.clear();
      let canvas = this.$refs.canvas;
      this.draw = new Draw(canvas);
      this.draw.draw();
      window.addEventListener('resize', this.onResize);
    },
    beforeDestroy: function () {
      window.removeEventListener('resize', this.onResize);
    },
    methods: {
      onResize() {
        this.$refs.canvas.style.removeProperty('height');
        this.$refs.canvas.style.removeProperty('width');
        this.draw.onResize();
        this.draw.draw();
      }
    }
  };
</script>



