<template>
  <div>
    <v-container grid-list-md>
      <v-layout wrap>
        <v-flex xs6>
          <v-text-field label="Название кроссворда" v-model="nonogramData.name"></v-text-field>
        </v-flex>
        <v-flex xs6>
          <v-text-field label="Название кроссворда (на английском)" v-model="nonogramData.nameEnglish"></v-text-field>
        </v-flex>
        <v-flex xs6>
          <v-text-field type="number" label="Число строк" v-model="numberRows" :min="nonogramSizeMin" :max="nonogramSizeMax" @input="recreateFieldDebounced()"></v-text-field>
        </v-flex>
        <v-flex xs6>
          <v-text-field type="number" label="Число столбцов" v-model="numberColumns" :min="nonogramSizeMin" :max="nonogramSizeMax" @input="recreateFieldDebounced()"></v-text-field>
        </v-flex>
        <v-flex xs6>
          <v-text-field type="number" label="Число цветов" v-model="numberColors" :min="numberColorsMin" :max="numberColorsMax" @input="updateNumberColorsDebounced()"></v-text-field>
        </v-flex>
        <v-flex xs12>
          <v-layout>
            <v-menu v-for="i of _.range(nonogramData.numberColors)" bottom offset-y :open-on-click="false" v-model="isColorPickerOpen[i]">
              <div
                slot="activator"
                :class="['color-square-wrapper mx-1', {'color-square-with-border': nonogramInput.currentColor === i}]"
                @contextmenu.prevent="showColorPicker(i)"
                @click="nonogramInput.changeColor(i)"
                :title="'нажмите, чтобы выбрать цвет\nнажмите правой кнопкой мыши чтобы изменить цвет'"
              >
                <div :class="['color-square']" :style="{backgroundColor: nonogramData.colors[i].hex}"></div>
              </div>
              <swatches-picker v-model="nonogramData.colors[i]" @input="$refs.nonogramCreatorField.updateAllCells()"></swatches-picker>
            </v-menu>
            <div class="buttons-change-cells-size">
              <v-btn flat icon title="сбросить масштаб" v-if="!isInitialScale()" @click="resetScale()">
                <v-icon medium>close</v-icon>
              </v-btn>
              <v-btn flat icon title="увеличить масштаб" @click="increaseScale()">
                <v-icon medium>add</v-icon>
              </v-btn>
              <v-btn flat icon title="уменьшить масштаб" @click="decreaseScale()">
                <v-icon medium>remove</v-icon>
              </v-btn>
            </div>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-container>

    <div class="field-container" ref="fieldContainer">
      <div class="field-border">
        <resize-sensor @resize="onResize()"></resize-sensor>
        <nonogram-creator-field ref="nonogramCreatorField" class="field"></nonogram-creator-field>
      </div>
    </div>

    <v-container style="display: flex">
      <v-btn>Проверить на решаемость</v-btn>
      <!--<v-btn @click="saveNonogramDataToLocalStorage()">Сохранить</v-btn>-->
      <!--<v-btn @click="loadNonogramDataFromLocalStorage()">Загрузить</v-btn>-->
      <v-btn @click="createNewNonogramData()" style="margin-left: auto">Создать другой кроссворд</v-btn>
    </v-container>
  </div>
</template>

<style lang="scss" scoped>
  $teal0: #009688;

  .color-square {
    display: inline-block;
    height: 100%;
    width: 100%;
    box-sizing: content-box;
  }

  .color-square-wrapper {
    height: 4vw;
    width: 4vw;
    padding: 0.5vw;
    transition: padding .1s;
  }

  .color-square-with-border {
    padding: 0;
  }

  .vc-swatches {
    height: initial;
    overflow-y: hidden;
  }

  .btn.btn--flat.btn--icon {
    margin: 0;
  }

  .buttons-change-cells-size {
    display: inline-block;
    margin-left: auto;
  }

  .field-container {
    overflow-x: auto;
    text-align: center;
  }

  .field-border {
    border: 3px solid $teal0;
    margin: 30px;
    display: inline-block;
  }

  .field {
    margin: 0;
  }
</style>

<script>
  import _ from 'lodash';
  import {Cell, State} from '../../model/cell';
  import NonogramData from './NonogramData';
  import NonogramInput from './NonogramInput';
  import NonogramCreatorField from './NonogramCreatorField.vue';
  import Vue from 'vue';
  import {nonogramCreatorStorage} from './NonogramCreatorStorage';

  function minmax(value, min, max) {
    return Math.min(max, Math.max(min, value));
  }

  export default {
    name: 'nonogram-creator',
    components: {NonogramCreatorField},
    data() {
      let data = {
        nonogramSizeMin: 5,
        nonogramSizeMax: 100,
        numberColorsMin: 1,
        numberColorsMax: 20,
        cellSizeScale: 1
      };
      data.nonogramData = nonogramCreatorStorage.getLastNonogram() || NonogramData.createNewNonogram(10, 15, 10);
      data.isColorPickerOpen = new Array(data.numberColorsMax).fill(false);
      data.nonogramInput = new NonogramInput(data.nonogramData, this);

      data.numberRows = data.nonogramData.numberRows;
      data.numberColumns = data.nonogramData.numberColumns;
      data.numberColors = data.nonogramData.numberColors;
      return data;
    },
    mounted() {
      window.addEventListener('resize', this.onResize);
      window.addEventListener('unload', this.saveNonogramDataToLocalStorage);
      this.$nextTick(() => this.onResize());
    },
    beforeDestroy: function () {
      window.removeEventListener('resize', this.onResize);
      window.removeEventListener('unload', this.saveNonogramDataToLocalStorage);
    },
    methods: {
      getFieldBorderSize(numberCells) {
        let outerBorder = 3 * 2;
        let usualBorder = 1 * (numberCells + 1);
        let fivethBorder = 2 * Math.floor((numberCells - 1) / 5);
        return outerBorder + usualBorder + fivethBorder;
      },
      getFieldBorderWidth() {
        return this.getFieldBorderSize(this.nonogramData.numberColumns);
      },
      updateStyle(cssText) {
        let styleTag = document.getElementById('fieldCellsStyle');
        if (!styleTag) {
          styleTag = document.createElement('style');
          styleTag.id = 'fieldCellsStyle';
          styleTag.type = 'text/css';
          styleTag.appendChild(document.createTextNode(cssText));
          document.head.appendChild(styleTag);
        } else {
          styleTag.childNodes[0].nodeValue = cssText;
        }
      },
      onResize: function () {
        if (!this.$refs.fieldContainer) {
          console.warn('this.$refs.fieldContainer === undefined');
          return;
        }
        let widthAll = this.$refs.fieldContainer.clientWidth;
        let widthTable = Math.round(widthAll * 0.77);
        let widthCells = widthTable - this.getFieldBorderWidth();
        let cellSize = Math.floor(widthCells / this.nonogramData.numberColumns * this.cellSizeScale);
        cellSize = Math.max(10, Math.min(50, cellSize));
        cellSize = Math.round(cellSize * this.cellSizeScale);
        this.updateStyle(`.field td { height: ${cellSize}px; width: ${cellSize}px; min-height: ${cellSize}px; min-width: ${cellSize}px; }`);
      },
      onCellClick(i, j) {
        this.nonogramInput.clickCoordinates(i, j);
        this.$refs.nonogramCreatorField.updateCell(i, j);
      },
      getCellColor(i, j) {
        let cell = this.nonogramData.field[i][j];
        let filled = cell !== -1;
        let selected = this.nonogramInput.isSelected(i, j);
        if (filled && selected) {
          return '#1de9b6';
        } else if (filled) {
          return this.nonogramData.colors[cell].hex;
        } else if (selected) {
          return '#00e5ff';
        } else {
          return '';
        }
      },
      recreateFieldDebounced: _.debounce(function () {
        this.numberRows = this.nonogramData.numberRows = minmax(this.numberRows, this.nonogramSizeMin, this.nonogramSizeMax);
        this.numberColumns = this.nonogramData.numberColumns = minmax(this.numberColumns, this.nonogramSizeMin, this.nonogramSizeMax);
        this.nonogramData.extendFieldIfRequired();
        this.onResize();
        this.$refs.nonogramCreatorField.updateAllCells();
      }, 700),
      updateNumberColorsDebounced: _.debounce(function () {
        this.numberColors = this.nonogramData.numberColors = minmax(this.numberColors, this.numberColorsMin, this.numberColorsMax);
        this.$refs.nonogramCreatorField.updateAllCells();
      }, 700),
      isInitialScale() {
        return Math.abs(this.cellSizeScale - 1) < 1e-2;
      },
      increaseScale() {
        this.cellSizeScale *= 1.1;
        this.onResize();
      },
      decreaseScale() {
        this.cellSizeScale /= 1.1;
        this.onResize();
      },
      resetScale() {
        this.cellSizeScale = 1;
        this.onResize();
      },
      showColorPicker(i) {
        Vue.set(this.isColorPickerOpen, i, true);
      },
      saveNonogramDataToLocalStorage() {
        let numberFilledCells = 0;
        for (let i = 0; i < this.nonogramData.numberRows; ++i) {
          for (let j = 0; j < this.nonogramData.numberColumns; ++j) {
            if (this.nonogramData.field[i][j] !== -1) {
              ++numberFilledCells;
            }
          }
        }
        let filledFactor = numberFilledCells / (this.nonogramData.numberRows * this.nonogramData.numberColumns);
//        let shouldSaveAsNewNonogram = filledFactor > 0.01 && numberFilledCells > 10;
        let shouldSaveAsNewNonogram = numberFilledCells > 0;
        let shouldSaveAsExistingNonogram = this.nonogramData.id !== undefined;
        if (shouldSaveAsNewNonogram || shouldSaveAsExistingNonogram) {
          nonogramCreatorStorage.saveNonogram(this.nonogramData);
        }
      },
      loadNonogramDataFromLocalStorage() {
        this.nonogramData = nonogramCreatorStorage.getLastNonogram();
        this.$refs.nonogramCreatorField.updateAllCells();
      },
      createNewNonogramData() {
        this.startEditAnotherNonogram(null);
      },
      startEditAnotherNonogram(nonogramId) {
        this.saveNonogramDataToLocalStorage();
        nonogramCreatorStorage.lastNonogramId = nonogramId;
        Object.assign(this.$data, this.$options.data.apply(this));
        this.$refs.nonogramCreatorField.updateAllCells();
      }
    }
  };
</script>



