import Vue from 'vue';
import Router from 'vue-router';
import NonogramCanvas from '@/components/NonogramCanvas';
import NonogramTable from '@/components/NonogramTable';
import NonogramComponent from '@/components/NonogramComponent';
import NonogramEditor from '@/components/creator/NonogramEditor';
import Test from '@/components/test/Test';
import Test2 from '@/components/test/Test2';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    // {path: '/', redirect: '/nonograms/create'},
    {path: '/', component: NonogramEditor},
    // {path: '/', redirect: '/nonograms/black-white/15504'},
    {path: '/nonograms/black-white/:id', component: NonogramComponent},
    {path: '/nonograms/create', component: NonogramEditor},
    {path: '/t', component: Test},
    // {path: '/t', component: Test2}
  ]
});



