<template>
  <v-container>
    <v-data-table
      v-if="nonograms.length > 0"
      :headers="headers"
      :items="nonograms"
      hide-actions
      class="elevation-1"
    >
      <template slot="items" slot-scope="props" v-ripple @click="startEditNonogram(props.item.id)">
        <tr v-ripple @click="startEditNonogram(props.item.id)">
          <td v-ripple class="text-xs-right">{{ props.item.name || `Нонограмма №${props.item.id + 1}` }}</td>
          <td class="text-xs-right">{{ props.item.numberRows }}</td>
          <td class="text-xs-right">{{ props.item.numberColumns }}</td>
          <td class="text-xs-right">{{ props.item.numberColors }}</td>
          <td class="text-xs-right">{{ moment(props.item.createTimestamp).format('DD MMMM, HH:mm') }}</td>
        </tr>
      </template>
    </v-data-table>
    <p v-else class="text-xs-center">
      Здесь будут отображаться кроссворды, создание которых ещё не завершено.
    </p>
  </v-container>
</template>

<style scoped>
  tr {
    cursor: pointer;
  }
</style>

<script>
  import {nonogramCreatorStorage} from './NonogramCreatorStorage';

  export default {
    props: ['startEditNonogram'],
    data() {
      return {
        headers: [
          {text: 'Название'},
          {text: 'Число строк'},
          {text: 'Число столбцоа'},
          {text: 'Число цветов'},
          {text: 'Дата создания'}
        ].map(header => Object.assign(header, {sortable: false})),
        nonograms: nonogramCreatorStorage.getAllNonograms()
      };
    }
//    methods: {
//      startEditNonogram(id) {
//        nonogramCreatorStorage.lastNonogramId = id;
//        this.$parent.activeTab = 'tabEditor';
//      }
//    }
  };
</script>



