<template>
  <table ref="table" class="field" @click="onTableClick($event)"></table>
</template>

<style lang="scss">
  $teal5: #e0f2f1;
  $teal4: #b2dfdb;
  $teal3: #80cbc4;
  $teal2: #4db6ac;
  $teal1: #26a69a;
  $teal0: #009688;

  table, tr, td {
    box-sizing: content-box;
  }

  table.field {
    table-layout: fixed;
    border-collapse: collapse;
  }

  table.field td {
    border: 1px solid $teal2;
  }

  table.field td:nth-of-type(5n + 5):not(:last-child) {
    border-right: 3px solid $teal2;
  }

  table.field tr:nth-of-type(5n + 5):not(:last-child) td {
    border-bottom: 3px solid $teal2;
  }
</style>

<script>
  export default {
    name: 'nonogram-creator-field',
    data() {
      return {};
    },
    mounted() {
      console.clear();
      this.changeTableSizeIfRequired();
      this.updateAllCells();
    },
    methods: {
      changeTableSizeIfRequired() {
        let nonogramData = this.$parent.nonogramData;
        let table = this.$refs.table;

//      изменяем число строк при необходимости
        for (let i = table.rows.length; i < nonogramData.numberRows; ++i) {
          table.insertRow();
        }
        while (table.rows.length > nonogramData.numberRows) {
          table.deleteRow(-1);
        }

//      изменяем число столбцов при необходимости
        for (let i = 0; i < nonogramData.numberRows; ++i) {
          let row = table.rows[i];
          for (let j = row.cells.length; j < nonogramData.numberColumns; ++j) {
            row.insertCell();
          }
          while (row.cells.length > nonogramData.numberColumns) {
            row.deleteCell(-1);
          }
        }
      },
      onTableClick(event) {
        let cell = event.target;
        let j = cell.cellIndex;
        let i = cell.parentElement.rowIndex;
        this.$parent.onCellClick(i, j);
        this.updateCell(i, j);
      },
      updateCell(i, j) {
        this.$refs.table.rows[i].cells[j].style.backgroundColor = this.$parent.getCellColor(i, j);
      },
      updateAllCells() {
        this.changeTableSizeIfRequired();
        let nonogramData = this.$parent.nonogramData;
        for (let i = 0; i < nonogramData.numberRows; ++i) {
          for (let j = 0; j < nonogramData.numberColumns; ++j) {
            this.updateCell(i, j);
          }
        }
      }
    }
  };
</script>



