export const State = {
  UNKNOWN: {id: 0, cssClass: 'unknown'},
  FILLED: {id: 1, cssClass: 'filled'},
  EMPTY: {id: 2, cssClass: 'empty'}
};
// State.UNKNOWN.next = State.FILLED;
// State.FILLED.next = State.UNKNOWN;
// State.FILLED.next = State.EMPTY;
// State.EMPTY.next = State.UNKNOWN;

export class Cell {
  constructor(i, j, color = 0) {
    // i, j --- координаты ячейки, [0..rows) [0..columns)
    this.i = i;
    this.j = j;
    // color --- номер цвета, [0..numberColors)
    this.color = color;
    this.state = State.UNKNOWN;
  }

  setUnknown() {
    this.state = State.UNKNOWN;
  }

  setFilled() {
    this.state = State.FILLED;
  }

  setEmpty() {
    this.state = State.EMPTY;
  }
}



