import {State} from '../../model/cell';

export default class NonogramInput {
  constructor(nonogramData, $parent) {
    this.$parent = $parent;
    this.nonogramData = nonogramData;
    this.resetState();
    this.bindKeyboardActions();
  }

  resetState() {
    this.currentColor = 0;
    this.selectedCell = {i: 0, j: 0};
    this.showSelectedCell = false;
  }

  bindKeyboardActions() {
    for (let color = 1; color <= 10; ++color) {
      let numberSymbol = String.fromCharCode('0'.charCodeAt() + (color % 10));
      Mousetrap.bind(numberSymbol, () => this.changeColor(color - 1));
    }
    Mousetrap.bind('up', () => this.moveSelectedCell(-1, 0));
    Mousetrap.bind('down', () => this.moveSelectedCell(+1, 0));
    Mousetrap.bind('left', () => this.moveSelectedCell(0, -1));
    Mousetrap.bind('right', () => this.moveSelectedCell(0, +1));
    Mousetrap.bind('f', () => this.keyboardToggleCurrentCell(), 'keydown');
    Mousetrap.bind('-', () => this.$parent.decreaseScale());
    Mousetrap.bind(['+', '='], () => this.$parent.increaseScale());

  }

  isSelected(i, j) {
    return this.showSelectedCell && this.selectedCell.i === i && this.selectedCell.j === j;
  }

  moveSelectedCell(deltaI, deltaJ) {
    let newI = Math.max(0, Math.min(this.nonogramData.numberRows - 1, this.selectedCell.i + deltaI));
    let newJ = Math.max(0, Math.min(this.nonogramData.numberColumns - 1, this.selectedCell.j + deltaJ));
    let oldI = this.selectedCell.i;
    let oldJ = this.selectedCell.j;
    this.selectedCell = {i: newI, j: newJ};
    this.showSelectedCell = true;
    this.$parent.$refs.nonogramCreatorField.updateCell(oldI, oldJ);
    this.$parent.$refs.nonogramCreatorField.updateCell(newI, newJ);
    // prevent default
    return false;
  }

  clickCoordinates(i, j) {
    this.showSelectedCell = false;
    this.toggleCell(i, j);
  }

  keyboardToggleCurrentCell() {
    this.showSelectedCell = true;
    this.toggleCell(this.selectedCell.i, this.selectedCell.j);
  }

  toggleCell(i, j) {
    let field = this.nonogramData.field;
    if (field[i][j] === this.currentColor) {
      field[i][j] = -1;
    } else {
      field[i][j] = this.currentColor;
    }
    this.selectedCell = {i, j};
    this.$parent.$refs.nonogramCreatorField.updateCell(i, j);
  }

  changeColor(color) {
    if (color < this.nonogramData.numberColors) {
      this.currentColor = color;
    }
  }
}



