export function create2darray(n, m) {
  let array = new Array(n);
  for (let i = 0; i < n; ++i) {
    array[i] = new Array(m);
  }
  return array;
}

export function transposeMatrix(matrix) {
  let numberRows = matrix.length;
  let numberColumns = matrix[0].length;
  let result = create2darray(numberColumns, numberRows);
  for (let i = 0; i < numberRows; ++i) {
    for (let j = 0; j < numberColumns; ++j) {
      result[j][i] = matrix[i][j];
    }
  }
  return result;
}



